:root {
  --board-rows: 7;
  --board-columns: 8;
  --tile-size: min( calc((100vw - 2rem * 2)  / var(--board-rows, 7) ), calc((100vh - 6rem * 2)  / var(--board-columns, 8) ) );
  --accent-color: #fcd53f;
}

body {
  height: 100vh;
  box-sizing: border-box;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: max(3rem, env(safe-area-inset-top) );
  padding-bottom: max(3rem, env(safe-area-inset-bottom) );
  background: url("background.e50e77c7.png") center / cover;
  flex-direction: column;
  justify-content: end;
  align-items: center;
  gap: 2rem;
  margin: 0;
  font-family: Saira Semi Condensed, sans-serif;
  font-weight: bold;
  display: flex;
}

.container {
  background-color: #475369;
  position: relative;
  overflow: hidden;
}

.logo {
  width: calc(var(--board-columns, 7)  * 2em);
  margin: 0 auto;
}

.statistics {
  width: 100%;
  max-width: calc(var(--tile-size, 8em)  * 8);
  color: var(--accent-color);
  text-shadow: -2px 2px #19253d75;
  font-size: 1.5rem;
  font-weight: bold;
}

.statistics > ul {
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.multiplier, .game-over {
  z-index: 1;
  pointer-events: none;
  opacity: 0;
  color: var(--accent-color);
  text-shadow: -4px 4px 4px #3a3a3a;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
  position: absolute;
  inset: 0;
}

.multiplier {
  font-size: 15rem;
}

.game-over {
  cursor: pointer;
  background-color: #34312f82;
  border: .5rem double;
  border-radius: .5rem;
  font-size: 4rem;
  transition: opacity .3s cubic-bezier(.4, 0, .2, 1);
}

.board {
  height: calc(var(--board-rows, 8)  * 1em);
  width: calc(var(--board-columns, 7)  * 1em);
  font-size: var(--tile-size, 8em);
  background-image: linear-gradient(#273346 1.5px, #0000 1.5px calc(100% - 1.5px), #273346 calc(100% - 1.5px)), linear-gradient(90deg, #273346 1.5px, #0000 1.5px calc(100% - 1.5px), #273346 calc(100% - 1.5px));
  background-size: calc(100% / var(--board-columns, 7) ) calc(100% / var(--board-rows, 8) );
  border: 1.5px solid #273346;
  border-radius: .5rem;
  position: relative;
}

.tile {
  cursor: pointer;
  width: 1em;
  height: 1em;
  box-sizing: border-box;
  text-shadow: -2px 4px 8px #525252;
  justify-content: center;
  align-items: center;
  transition: left .3s cubic-bezier(.4, 0, .2, 1), top .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
}

.tile:before, .tile:after {
  content: "";
  width: 1em;
  height: 1em;
  background-size: cover;
}

.tile:after {
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAAAXNSR0IArs4c6QAAAAtJREFUGFdjYAACAAAFAAGq1chRAAAAAElFTkSuQmCC");
  transition: background-image .3s cubic-bezier(.4, 0, .2, 1);
